.anchorHome:hover + .homeLink{
display: flex !important;
}
.anchorAbout:hover + .aboutLink{
    display: flex !important;
    }

    .anchorVideos:hover + .videosLink{
        display: flex !important;
        }

        .anchorKernel:hover + .kernelLink{
            display: flex !important;
            }

            .anchorContact:hover + .contactLink{
                display: flex !important;
                }
                




#line{
    width: 3px;
    height: 100%;
    background: white;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.dots{
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: white;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    /* transition: all 0.3s; */
    /* margin-left: 20px; */
}
/* #line span{
    position: absolute;
} */
.active{
    outline: 3px solid white;
    transition: all 0.1s;
    outline-offset: 3px;
}
.PageHeader{

    background: rgba(4, 4, 4, 0.3);
    color: white;
    font-family: 'Forgotten Futurist';
    transform: translate(-75px, -15px);
    font-size: 16px;
    position: relative;
    top: -5px;
    white-space: nowrap;
    /* display: none; */

}
a {
    text-decoration: none;
}
#PageHeader1 {
    left: -70px;
}
#PageHeader2 {
    left: -90px;
}
#PageHeader3 {
    left: -70px;
}
#PageHeader4 {
    left: -120px;
}
#PageHeader5 {
    left: -90px;
}
#PageHeader6 {
    left: -95px;
}
@media screen and (max-width: 768px) {
    #pagination{
        display: none
    }
}